import React, { Component } from "react";
import helpers from "./helperFunctions.js";

const lapStyle = {
  color: "#ff901f",
  margin: "5px",
  alignItems: "center",
  fontSize: "25px",
  textShadow: "2px 2px 5px white",
  fontFamily: "Century Gothic ",
  listStyle: "none"
};

const workingButtonStyle = {
  fontFamily: "Century Gothic",
  backgroundColor: "#65d80a",
  padding: "15px 32px",
  margin: "4px 2px",
  color: "white",
  fontSize: "15px",
  border: "none",
  borderRadius: "8px",
  boxShadow: "0px 4px 4px 0px #f222ff"
};

const disabledButtonStyle = {
  fontFamily: "Century Gothic",
  backgroundColor: "#ff901f",
  padding: "15px 32px",
  margin: "4px 2px",
  color: "white",
  fontSize: "15px",
  border: "none",
  borderRadius: "8px",
  boxShadow: "0px 4px 4px 0px #ffd319"
};

class Timer extends Component {
  constructor(props) {
    super(props);
    this.state = { count: 0, laps: [], timerRunning: false };
  }
  //Timer body
  componentWillUnmount() {
    clearInterval(this.timer);
  }
  tick() {
    this.setState({ count: this.state.count + 1 });
  }
  startTimer() {
    clearInterval(this.timer);
    this.timer = setInterval(this.tick.bind(this), 10);
  }
  stopTimer() {
    clearInterval(this.timer);
  }
  resetTimer() {
    this.setState({ count: 0 });
    this.setState({ laps: [] });
  }
  handleLap() {
    var currentCount = helpers.TimerFormat(this.state.count);
    this.setState({ laps: this.state.laps.concat(currentCount) });
  }
  //Add laps
  lapsOutput() {
    if (this.state.laps.length === 0) {
      return null;
    } else {
      return (
        <ul>
          {this.state.laps.map(laps => (
            <li style={lapStyle} key={laps}>
              {laps}
            </li>
          ))}
        </ul>
      );
    }
  }
  //buttons
  startAction() {
    if (this.state.timerRunning === true) {
      return null;
    } else {
      return (
        <button
          style={workingButtonStyle}
          onClick={() => {
            this.startTimer();
            this.setState({ timerRunning: true });
          }}
        >
          Start
        </button>
      );
    }
  }
  stopAction() {
    if (this.state.timerRunning === false) {
      return null;
    } else {
      return (
        <button
          style={workingButtonStyle}
          onClick={() => {
            this.stopTimer();
            this.setState({ timerRunning: false });
          }}
        >
          Stop
        </button>
      );
    }
  }
  lapAction() {
    if (this.state.timerRunning === true) {
      return (
        <button style={workingButtonStyle} onClick={this.handleLap.bind(this)}>
          Lap
        </button>
      );
    } else {
      return <button style={disabledButtonStyle}>Lap</button>;
    }
  }
  resetAction() {
    if (this.state.timerRunning === false) {
      return (
        <button style={workingButtonStyle} onClick={this.resetTimer.bind(this)}>
          Reset
        </button>
      );
    } else {
      return <button style={disabledButtonStyle}>Reset</button>;
    }
  }
  timerState() {
    if (this.state.timerRunning === false) {
      return (
        <h1
          style={{
            color: "white",
            fontFamily: "Century Gothic",
            textShadow: "1px 1px 4px #F148FB"
          }}
        >
          {helpers.TimerFormat(this.state.count)}
        </h1>
      );
    } else {
      return (
        <h1
          style={{
            color: helpers.CountToRGB(this.state.count),
            fontFamily: "Century Gothic",
            textShadow: "4px 4px 5px #F148FB"
          }}
        >
          {helpers.TimerFormat(this.state.count)}
        </h1>
      );
    }
  }
  render() {
    return (
      <div className="timer">
        {this.timerState()}
        <div>
          {this.startAction()}
          {this.stopAction()}
          {this.lapAction()}
          {this.resetAction()}
        </div>
        <div>{this.lapsOutput()}</div>
      </div>
    );
  }
}
export default Timer;
